import React, {Component} from 'react';
import './CustomButtons.css';
import Button from 'react-bootstrap/Button';

export class BorderlessRadioBox extends Component {

  render() {

    const { name, children } = this.props;

    return (
      <label className='radiocontainer'>
        <input
        ref={this.props.refCall || (() => {})}
        onClick={this.props.callback || (() => {})}
        radioid={this.props.radioId}
        type='radio' name={name}/>
        <span>{children}</span>
      </label>
    )
  }
}

export class BorderlessCheckBox extends Component {

  render() {

    const { name, children, spanStyle } = this.props;

    return (
      <label className='checkboxcontainer'>
        <input
        ref={this.props.refCall || (() => {})}
        onClick={this.props.callback || (() => {})}
        type='checkbox' name={name}
        />
        <span
        style={spanStyle}
        >{children}</span>
      </label>
    )
  }
}

export class SharpEdgeButton extends Component {

  render() {

    let { style, onClick, name } = this.props;

    if (!style) style = {};
    style['borderRadius'] = '0px';
    style['borderColor'] = 'transparent';

    return (
      <Button style={style} name={name}
      onClick={onClick || (() => {})}>
        {this.props.children}
      </Button>
    )
  }
}

