export function hsv2rgb(h, s, v) {
  let main_val = {};
  s = s / 100;
  v = v / 100;
  let d_s = 1 - s;

  let h_part = h % 120; h_part = (60 - Math.abs(h_part - 60)) * 255 / 60;
  h_part = Math.floor(h_part);

  switch (Math.floor(h / 60) % 6) {
    case 0:
      main_val = {r : 255, g : h_part, b : 0};
      break;
    case 1:
      main_val = {r : h_part, g : 255, b : 0};
      break;
    case 2:
      main_val = {r : 0, g : 255, b : h_part};
      break;
    case 3:
      main_val = {r : 0, g : h_part, b : 255};
      break;
    case 4:
      main_val = {r : h_part, g : 0, b : 255};
      break;
    case 5:
      main_val = {r : 255, g : 0, b : h_part};
      break;
    default:
      break;
  }

  let final_val = {};
  for (const key in main_val) {
    final_val[key] = s * main_val[key] + d_s * 255;
    final_val[key] = Math.round(v * final_val[key]);
  }

  return final_val;
}

export function rgb2hsv(r, g, b) {
  let rgb = { r, g, b };
  const takeKeyVal = (colors, hint) => {
    const val = Math[hint](...Object.values(colors));
    const [ key ] = (() => {
      const [ return_ ] = 
        Object.entries(colors).filter(([key_, val_]) => { return val == val_; });
      return return_;
    })();
    delete colors[key];

    return {
      key, val,
    };
  };
  const { key : maxKey, val : maxVal } = takeKeyVal(rgb, 'max');
  const { key : minKey, val : minVal } = takeKeyVal(rgb, 'min');
  const { val : midVal } = takeKeyVal(rgb, 'min');

  const val = Math.round(maxVal / 255 * 100);
  const sat = Math.round((255 - minVal) / 255 * 100);
  const angleOffset = midVal / 255 * 60;

  let hue;
  switch (maxKey + minKey) {
    case 'rb':
      hue = angleOffset;
      break;
    case 'gb':
      hue = 120 - angleOffset;
      break;
    case 'gr':
      hue = 120 + angleOffset;
      break;
    case 'br':
      hue = 240 - angleOffset;
      break;
    case 'bg':
      hue = 240 + angleOffset;
      break;
    case 'rg':
      hue = 360 - angleOffset;
      break;
    default:
      break;
  }

  return { h : hue, s : sat, v : val };
}

export function rgb2hex(r, g, b) {
  const pad = (num) => { return (num.length < 2) ? '0' + num : num; }
  return '' + pad(r.toString(16)) + pad(g.toString(16)) + pad(b.toString(16));
}

export function hex2rgb(hex) {
  const r = hex.slice(0,2);
  const g = hex.slice(2,4);
  const b = hex.slice(4,6);
  return {
    r : parseInt(r, 16),
    g : parseInt(g, 16),
    b : parseInt(b, 16),
  };
}

