import React, {Component} from 'react';
import './Child.css';
import {BorderlessCheckBox} from '../menu/CustomButtons';

export class UniversalEditor extends Component {
  render() {

    const { callback } = this.props;
    const simpleChangeValue = (event) => {
      callback(event.target.name)(event.target.value);
    }

    return (
      <div>
        <div>
        <label>X : &nbsp;</label>
        <input style={{ width : '70%' }} type='number' name='x'
          value={this.props.x || '0'}
          onChange={simpleChangeValue}
        />
        </div>
        <div>
        <label>Y : &nbsp;</label>
        <input style={{ width : '70%' }} type='number' name='y'
          value={this.props.y || '0'}
          onChange={simpleChangeValue}
        />
        </div>
      </div>
    )
  }
}

export class TextEditor extends Component {

  state = {
    displayTextarea : false
  }

  toggleTextArea = () => {
    this.setState({
      displayTextarea : !this.state.displayTextarea
    });
  }

  render() {

    const { displayTextarea } = this.state;
    const { callback } = this.props;
    const textAreaStyle = {
      display : (displayTextarea) ? 'block' : 'none'
    }
    const textButtonStyle = {
      backgroundColor: 'grey'
    }

    const flexChild = {
      display : 'inline-flex',
      alignItems : 'flex-start'
    }

    const simpleChangeValue = (event) => {
      callback(event.target.name)(event.target.value);
    }

    return (
      <div id='childeditor-container'>
        <label>Width : &nbsp;
        <input type='number' name='width' value={this.props.width || 0} onChange={simpleChangeValue} />
        </label>
        &nbsp;
        <label>Font Size : &nbsp;
        <input type='number' name='fontSize' value={this.props.fontSize || 0} onChange={simpleChangeValue} />
        </label>
        &nbsp;
        <label>Align : &nbsp;
        <select name='align' id='editor-text-align' value={this.props.align} onChange={simpleChangeValue}>
          <option value='left'>Left</option>
          <option value='right'>Right</option>
          <option value='center'>Center</option>
          <option value='justify'>Justify</option>
        </select>
        </label>
        &nbsp;
        <label>
        <div>
        <input
          ref={ref => this.textAreaButton = ref}
          onClick={this.toggleTextArea}
          type='submit' value='Text'
        />
        </div>
        <div id='textarea-container'>
        <textarea style={textAreaStyle}
          name='text' value={this.props.text} onChange={simpleChangeValue}
        /></div>
        </label>
      </div>
    )
  }
}

export class ImageEditor extends Component {

  state = {
    displayImageInput : false
  }

  selectFile = (event) => {

    const { fileInput } = this.props;
    if (fileInput) {
      console.log(fileInput);
      fileInput.click();
    }
  }

  imageUpload = (event) => {

    let file = this.uploadInput.files[0];
    if (file) {

      const { callback } = this.props;
      let fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload = (e) => {
        let newImage = new Image();
        newImage.src = e.target.result;
        newImage.onload = () => {
          callback('image')(newImage)
        }
      }
    }
  }

  toggleImageInput = (event) => {
    this.setState({
      displayImageInput : !this.state.displayImageInput
    });
  }

  render() {

    const { displayImageInput } = this.state;
    const imageInputStyle = {
      display : (displayImageInput) ? 'flex' : 'none'
    }

    return (
      <div id='childeditor-container'>
        <label>
        <div>
        <input type='submit' value='Select' onClick={this.toggleImageInput} />
        </div>
        <div id='imageinput-container' style={imageInputStyle}>
        <input ref={ref => this.uploadInput = ref } type='file' accept='image/*' onChange={this.imageUpload}/>
        <label>
        <input type='submit' value='Select' style={{ marginRight:'10px' }}/>
        <input ref={ref => this.urlInput = ref} type='text' />
        </label>
        </div>
        </label>
      </div>
    )
  }
}

