import React, {Component} from 'react';
import Button from 'react-bootstrap/Button';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faCircle, faImage} from '@fortawesome/free-regular-svg-icons';

import './Toolbar.css'

import {
  hsv2rgb, rgb2hsv
} from '../../utils/Colors'
import {
  EnableSelect, DisableSelect
} from '../../utils/Select';

export class ToolButton extends Component {

  state = {}

  onMouseEnter = (event) => {
    this.setState({
      showTip : true
    })
  }

  onMouseLeave = (event) => {
    this.setState({
      showTip : false
    })
  }

  render() {

    const { showTip } = this.state;
    const { tips, tipStyle, noMargin } = this.props;
    const toolStyle = {
      paddingLeft : '0',
      paddingRight : '0',
      marginRight : '5px'
    }

    return (
      <Button onClick={this.props.onClick} style={toolStyle} className='tool'
      onMouseEnter={this.onMouseEnter}
      onMouseLeave={this.onMouseLeave}
      >
        { showTip && <ToolTip noMargin={noMargin} style={tipStyle}>{tips || 'Default Tip'}</ToolTip> }
        {this.props.children}
      </Button>
    )
  }
}

export class ToolTip extends Component {

  onClick = (event) => {
  }

  reConfigure = ( ref ) => {
    if (!this.props.noMargin && ref) {
      let parent = ref.parentElement;
      const { width } = parent.getBoundingClientRect();
      ref.style.marginLeft = `${width/2}px`;
    }
  }

  render() {

    let style = this.props.style || {};

    style['position'] = 'absolute';

    return (
      <span ref={this.reConfigure} className='tooltiptext' style={style} onClick={this.onClick}>{this.props.children}</span>
    )
  }
}

export class ColorPicker extends Component {

  state = {
    hueSelectorLeft : 0,
    satSelectorPos : 0,
    valSelectorPos : 0,
    cornerRGB : { r: 255, g: 0, b:0 }
  }

  constructor (props) {
    super(props);

    this.barWidth = parseInt(props.width.replace(/[^-0-9]/g, ''));
  }

  hueSelectRef = (element) => {
    if (element) {
      this.hueSelector = element;
      const rect = element.getBoundingClientRect();
      this.hueRect = rect;
    }
  }

  svSelectRef = (element) => {
    if (element) {
      this.svSelector = element;
      const rect = this.svSelector.parentElement.getBoundingClientRect();
      this.svRect = rect;
    }
  }

  doCallback = () => {
    const realCallBack = () => {
      const { getColorCallback } = this.props;
      const { getRGBCallback, getHSVCallback } = this.props;
      if (getColorCallback) {
        getColorCallback({
          rgb: this.getRGB()
        });
      }
      if (getRGBCallback) {
        getRGBCallback(this.getRGB());
      }
      if (getHSVCallback) {
        
      }
    }

    realCallBack();
  }

  getHSV = () => {
    const h = this.state.hueSelectorLeft / this.barWidth * 360;
    const s = this.state.satSelectorPos / this.barWidth * 100;
    const v = (1 - (this.state.valSelectorPos / this.barWidth)) * 100;
    return { h, s, v };
  }

  setHSV = (color) => {
    const { h, s, v } = color;
    this.setState({
      hueSelectorLeft : h / 360 * this.barWidth,
      satSelectorPos : s / 100 * this.barWidth,
      valSelectorPos : (100 - v) / 100 * this.barWidth,
      cornerRGB : hsv2rgb(h, 100, 100),
    });
  }

  getRGB = () => {
    const { h, s, v } = this.getHSV();
    return hsv2rgb(h, s, v);
  }

  setRGB = (color) => {
    this.setHSV(rgb2hsv(color.r, color.g, color.b));
  }

  clickHueSelector = () => {
    DisableSelect();
    const rect = this.hueSelector.parentElement.getBoundingClientRect();
    this.hueRect = rect;
    const mouseMove = (event) => {
      const newLeft = event.clientX - this.hueRect.left;
      const selectedLeft = Math.max(Math.min(this.barWidth, newLeft), 0);
      const cornerRGB = hsv2rgb(selectedLeft / this.barWidth * 360, 100, 100);
      this.setState({
        hueSelectorLeft : selectedLeft,
        cornerRGB
      }, () => {

        this.doCallback();
      });
    }
    const mouseUp = () => {
      document.body.removeEventListener('mousemove', mouseMove);
      document.body.removeEventListener('mouseup', mouseUp);

      EnableSelect();
    }
    document.body.addEventListener('mousemove', mouseMove);
    document.body.addEventListener('mouseup', mouseUp);
  }

  clickSVSelector = () => {
    DisableSelect();
    const rect = this.svSelector.parentElement.getBoundingClientRect();
    this.svRect = rect;
    const mouseMove = (event) => {
      const newLeft = event.clientX - this.svRect.left;
      const newTop = event.clientY - this.svRect.top;
      this.setState({
        satSelectorPos : Math.max(Math.min(this.barWidth, newLeft), 0),
        valSelectorPos : Math.max(Math.min(this.barWidth, newTop), 0),
      }, () => {

        this.doCallback();
      });
    }
    const mouseUp = () => {
      document.body.removeEventListener('mousemove', mouseMove);
      document.body.removeEventListener('mouseup', mouseUp);

      EnableSelect();
    }
    document.body.addEventListener('mousemove', mouseMove);
    document.body.addEventListener('mouseup', mouseUp);
  }

  componentDidMount() {
    const { assignSetHSV, assignSetRGB } = this.props;
    if (assignSetHSV) assignSetHSV(this.setHSV);
    if (assignSetRGB) assignSetRGB(this.setRGB);
  }

  render() {

    const { refPickerCallback } = this.props;
    const { hueSelectorLeft, satSelectorPos, valSelectorPos } = this.state;

    const hueBarStyle = {
      width : this.props.width,
      height : '15px',
    }

    const svBoxStyle = {
      width: this.props.width,
      height: this.props.width,
      background: `linear-gradient(to top, black, transparent) , linear-gradient(to right, white, ${quick2css(this.state.cornerRGB)})`
    }

    const hueSelectorStyle = {
      left : hueSelectorLeft,
    }

    const svSelectorStyle = {
      left: satSelectorPos,
      top: valSelectorPos
    }

    return (
      <div ref={refPickerCallback || (() => {})} id='color-picker-container'>
        <div id='hue-bar' style={hueBarStyle}>
          <div ref={this.hueSelectRef} id='hue-selector' style={hueSelectorStyle} onMouseDown={this.clickHueSelector}>
          </div>
        </div>
        <div id='sv-box' style={svBoxStyle}>
          <div ref={this.svSelectRef} id='sv-selector' style={svSelectorStyle} onMouseDown={this.clickSVSelector}></div>
        </div>
      </div>
    )
  }
}

function quick2css(val) {
  return `rgb(${val.r}, ${val.g}, ${val.b})`;
}

export class PhotoToolbar extends Component {
  render() {

    const buttonStyle = {
      padding : '0px',
      width : '100%',
      height : '100%'
    }

    const flexBoxStyle = {
      display : 'flex',
      flexWrap : 'wrap',
    }

    return (
      <div style={flexBoxStyle}>
        <div className='toolBarButton' >
        <Button style={buttonStyle} onClick={this.props.addCircle} ><FontAwesomeIcon icon={faCircle} /></Button>
        </div>
        <div className='toolBarButton' >
        <Button style={buttonStyle} onClick={this.props.addText} >T</Button>
        </div>
        <div className='toolBarButton' >
        <Button style={buttonStyle} onClick={this.props.addImage} ><FontAwesomeIcon icon={faImage} /></Button>
        </div>
      </div>
    )
  }
}

