import React, {Component} from 'react';
import {MemoryRouter as Router, Switch, useRouteMatch, Route, withRouter} from 'react-router-dom';
import PhotoEditor from './Photo';

class Editor extends Component {

  render() {
    const { path, url } = this.props.match;
    const divBarStyle = {
      marginBottom: '10px',
      background: 'black'
    }

    return (
      <>
      <div style={divBarStyle}>&nbsp;</div>
      <Switch>
        <Route path={`${path}/photo`} component={PhotoEditor} />
      </Switch>
      </>
    )
  }
}

export default withRouter(Editor);
