import React, {Component} from 'react';
import MyCanvas from './Canvas'
import {BorderlessRadioBox} from '../menu/CustomButtons';
import Texich from './Texich';

export default class PhotoEditor extends Component {

  state = {
    selectedTabKey : 'main',
    init : true
  }

  refCollection = {}
  tabContentRefCollection = {}
  tabKeyCollection = [
    'main',
    'txic'
  ]

  constructor (props) {
    super(props);
  }

  changeTab = (event) => {
    const tabKey = event.target.getAttribute('radioid');
    this.tabKeyCollection.forEach((key) => {
      const tab = this.tabContentRefCollection[key];
      if (tab) {
        tab.style.display = (key == tabKey) ? 'flex' : 'none';
      }
    });
  }

  assignRef = (key) => {
    const realAssign = (ref) => {
      if (ref) {
        this.refCollection[key] = ref;
      }
    }

    return realAssign;
  }

  assignTab = (key) => {
    const realAssign = (ref) => {
      if (ref) {
        this.tabContentRefCollection[key] = ref;
      }
    }

    return realAssign
  }

  componentDidMount () {
    if (this.state.init) {
      this.setState({ init : false })
      this.refCollection['main'].click();
    }
  }

  render() {

    const tabBarStyle = {
      display: 'inline-flex'
    }

    return (
      <div id='editor' className='row'>
        <div className='col-12' style={tabBarStyle}>
          <BorderlessRadioBox callback={this.changeTab} radioId='main' refCall={this.assignRef('main')} name='tab' checked>
            Main Tab
          </BorderlessRadioBox>
          <BorderlessRadioBox callback={this.changeTab} radioId='txic' refCall={this.assignRef('txic')} name='tab'>
            Texich
          </BorderlessRadioBox>
        </div>
        <div className='col-12'>
          <MyCanvas refCall={this.assignTab('main')} />
          <Texich refCall={this.assignTab('txic')} />
        </div>
      </div>
    )
  }
}
