import React, {Component} from 'react';
import {ToolButton, ColorPicker} from './Toolbar';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faPlusSquare} from '@fortawesome/free-regular-svg-icons';
import './Texich.css';
import {SharpEdgeButton} from '../menu/CustomButtons';

import {
  MultipleSelectItemCtrlShift as selectItem
} from '../../utils/Select';
import {
  DuplicateObject as copyObj
} from '../../utils/Copy'
import {
  hex2rgb, rgb2hex
} from '../../utils/Colors'

export default class Texich extends Component {

  state = {
    boxWidth : 300,
    boxHeight : 300,
    paragraphs : [],
    selectedParagraphList : [],
    selectedChildList : [],
    childCounter : 0,
    childColorKey : 'Color',
  }

  childCounter = 0
  paragraphCounter = 0

  changeSize = (event) => {
    this.setState({
      [event.target.name] : event.target.value
    });
  }

  createChild = (id) => {
    return {
      id,
      name : `Child ${id}`,
      style : {
        color: '#000000'
      },
      text : '',
    };
  }

  addParagraph = (event) => {

    const newParagraph = {
      id : this.paragraphCounter,
      name : `Parg ${this.paragraphCounter}`,
      style : {
        textAlign : 'left',
        textIndent : '0px'
      },
      childCounter : 1,
      child : [this.createChild(0)]
    };

    this.setState({
      paragraphs : this.state.paragraphs.concat(newParagraph),
    });

    this.paragraphCounter++;
  }

  addChild = (event) => {

    const { selectedParagraph } = this.state;
    if (selectedParagraph) {
      selectedParagraph.child.push(
        this.createChild(selectedParagraph.childCounter)
      );

      selectedParagraph.childCounter++;

      this.setState({
        selectedParagraph
      });
    }
  }

  selectParagraph = (event) => {
    const paragraphKey = parseInt(event.target.name);
    const { shiftKey, ctrlKey } = event;
    const { selectedItemList : selectedParagraphList, selectedItem : selectedParagraph } = selectItem({
      itemKey : paragraphKey,
      itemList : this.state.paragraphs,
      ctrlKey : ctrlKey || false,
      shiftKey : shiftKey || false,
      selectedItem : this.state.selectedParagraph,
      selectedItemList : this.state.selectedParagraphList
    });

    this.setState({
      selectedParagraph,
      selectedParagraphList,
      selectedChild : undefined,
      selectedChildList : [],
    });
  }

  selectChild = (event) => {
    const childKey = parseInt(event.target.name);
    const { shiftKey, ctrlKey } = event;
    const { selectedItemList : selectedChildList, selectedItem : selectedChild } = selectItem({
      itemKey : childKey,
      itemList : this.state.selectedParagraph.child,
      ctrlKey : ctrlKey || false,
      shiftKey : shiftKey || false,
      selectedItem : this.state.selectedChild,
      selectedItemList : this.state.selectedChildList
    });

    this.setState({
      selectedChild,
      selectedChildList,
    }, () => {

      this.changeChildColorKey('color');
    });
  }

  editParagraphStyle = (key, value) => {

    let { selectedParagraph } = this.state;
    if (selectedParagraph) {
      selectedParagraph.style[key] = value;

      this.setState({
        selectedParagraph
      });
    }
  }

  editParagraphStyleKey = (key) => {
    const editStyleVal = (val) => {
      this.editParagraphStyle(key, val);
    }

    return editStyleVal;
  }

  editChildKeyVal = (key, value) => {
    let { selectedChild } = this.state;

    selectedChild[key] = value;

    this.setState({
      selectedChild
    });
  }

  editChildKey = (key) => {
    const editChildVal = (val) => {
      this.editChildKeyVal(key, val);
    }

    return editChildVal;
  }

  changeChildColorKey = (key) => {

    this.setState({
      childColorKey : key
    });


    const { selectedChild, selectedChildList } = this.state;
    if (selectedChildList.length == 1) {

      const hex = selectedChild.style[key].replace('#', '');
      this.setChildRGB(hex2rgb(hex));
    }
  }

  setChildColorRGB = (color) => {

    const { selectedChild, childColorKey } = this.state;
    console.log(color);
    if (selectedChild) {
      selectedChild.style[childColorKey] = '#' + rgb2hex(color.r, color.g, color.b);
      this.setState({
        selectedChild
      });
    }
  }

  render() {

    const {
      selectedChild, selectedChildList, childColorKey,
      selectedParagraph, paragraphs, selectedParagraphList
    } = this.state;

    let textAreaProps = {};

    if (selectedChildList.length != 1) {
      textAreaProps['disabled'] = 'disabled';
    }

    const textAreaStyle = {
      position : 'relative',
      resize : 'vertical',
      width : '100%',
      height : '30vh',
    }

    const textBoxStyle = {
      width : this.state.boxWidth + 'px',
      height : this.state.boxHeight + 'px',
    }

    const item = { renderer:'span' };

    const changeParagraphStyle = (event) => {
      this.editParagraphStyleKey(event.target.name)(event.target.value);
    }

    const simpleEditChild = (event) => {
      this.editChildKey(event.target.name)(event.target.value);
    }

    const changeTextStyle = (event) => {
      const { name, value } = event.target;
    }

    return (
      <div ref={this.props.refCall} className='row'>
        <div id='textbox-properties' className='col-12'>
          <label>Width :</label>
          <input type='number' name='boxWidth' value={this.state.boxWidth} onChange={this.changeSize}/>
          <label>Height :</label>
          <input type='number' name='boxHeight' value={this.state.boxHeight} onChange={this.changeSize}/>
        </div>
        <div className='col-12'>
          <ToolButton noMargin onClick={this.addParagraph} tips='Add Paragraph'>
            <div>P</div>
          </ToolButton>
          <ToolButton noMargin onClick={this.addChild} tips='Add Text Child'>
            <div>T</div>
          </ToolButton>
        </div>
        <div id='textbox-container' className='col-4'>
          <div style={{ margin:'0px' }}>
            <textarea {...textAreaProps} style={textAreaStyle} name='text'
              value={(selectedChild) ? selectedChild.text : 'Place Holder'}
              onChange={simpleEditChild} />
          </div>
          <div style={{ display:'flex', width:'100%' }}>
            <div style={{ width:'50%' }}>
              <ColorPicker
              getRGBCallback={this.setChildColorRGB}
              assignSetRGB={func => this.setChildRGB = func}
              assignSetHSV={func => this.setChildHSV = func}
              width='200px'/>
            </div>
            <div style={{ width:'50%' }}>
            { (selectedChildList.length == 1) &&
              <select
              onChange={(e) => this.changeChildColorKey(e.target.value)}
              value={childColorKey}>
                <option value='color'>Color</option>
              </select>
            }
            </div>
          </div>
          <div style={{ display:'flex', width:'100%', marginTop:'50px' }}>
            <div style={{ width:'40%' }}>
            { (selectedParagraph) &&

              <select name='textAlign' id='textalign'
              onChange={changeParagraphStyle}
              value={selectedParagraph.style.textAlign}>
                <option value='left'>Left</option>
                <option value='right'>Right</option>
                <option value='center'>Center</option>
                <option value='justify'>Justify</option>
              </select>
            }
            </div>
            <div style={{ width:'5%' }}>&nbsp;</div>
            <div style={{ width:'25%' }}>
            { (selectedParagraphList.length == 1) &&
              selectedParagraph.child.map((item, idx) =>
              <SharpEdgeButton key={item.id} name={item.id}
              onClick={this.selectChild}
              style={{
                width:'100%',
                background : (selectedChildList.includes(item.id)) ? 'green' : '#007bff' }}>
              {item.name}</SharpEdgeButton>)
            }
            </div>
            <div style={{ width:'5%' }}>&nbsp;</div>
            <div style={{ width:'25%' }}>
            {
              paragraphs.map((item, idx) =>
              <SharpEdgeButton key={item.id} name={item.id}
              onClick={this.selectParagraph}
              style={{
                width:'100%',
                background : (selectedParagraphList.includes(item.id)) ? 'green' : '#007bff' }}>
              {item.name}</SharpEdgeButton>)
            }
            </div>
          </div>
        </div>
        <div id='textbox-container' className='col-8'>
          <div id='textbox-border'>
            <div id='textbox-real' style={textBoxStyle}>
            { paragraphs.map((item, idx) =>
              <p key={idx} style={copyObj(item.style)}>
              { item.child.map((child, cdx) => 
                <span key={cdx} style={copyObj(child.style)}>
                {child.text || 'Default Text'}</span>
              ) }
              </p>
            ) }
            </div>
          </div>
        </div>
      </div>
    )
  }
}

