
export function MultipleSelectItemCtrlShift(params) {

  let { itemKey, itemList, ctrlKey, shiftKey, selectedItem, selectedItemList } = params;

  let newKey;
  if (selectedItem && shiftKey) {
    const itemIndex = (() => {
      for (const key in itemList) {
        if (itemList[key].id == itemKey) return parseInt(key);
      }
    })();
    const selectedIndex = (() => {
      for (const key in itemList) {
        if (itemList[key].id == selectedItem.id) return parseInt(key);
      }
    })();

    const startIndex = Math.min(itemIndex, selectedIndex), endIndex = Math.max(itemIndex, selectedIndex);
    if (!ctrlKey) selectedItemList = [];
    let indexCounter = startIndex;
    while (indexCounter <= endIndex) {
      const currentItem = itemList[indexCounter++];
      if (!selectedItemList.includes(currentItem.id)) {
        selectedItemList.push(currentItem.id);
      }
    }
  } else if (ctrlKey) {
    if (selectedItemList.includes(itemKey)) {

      selectedItemList = selectedItemList.filter((item) => { return item != itemKey });
    } else {
      selectedItemList.push(itemKey);
    }
    [ newKey ] = selectedItemList.slice(-1) || [ -1 ];
  } else {
    selectedItemList = (selectedItemList.length == 1 && selectedItem.id == itemKey) ? [] : [itemKey];
    [ newKey ] = selectedItemList.slice(-1) || [ -1 ];
  }
  if (!shiftKey) {

    [ selectedItem ] = itemList.filter((item) => { return item.id == newKey });
  }

  return {
    selectedItemList,
    selectedItem
  };
}

export function DisableSelect() {
  document.body.style.userSelect = 'none';
}

export function EnableSelect() {
  document.body.style.userSelect = 'unset';
}

