import React, {Component} from 'react';
import FontFaceObserver from 'fontfaceobserver';
import {Stage, Text, Layer, Circle, Image as KonvaImage } from 'react-konva';
import Button from 'react-bootstrap/Button';
import './Canvas.css';

import {
  MultipleSelectItemCtrlShift as selectItem
} from '../../utils/Select'

import {
  PhotoToolbar, ColorPicker, ToolTip, ToolButton
} from './Toolbar';

import {
  TextEditor, UniversalEditor, ImageEditor
} from './Child'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faPlusSquare, faTrashAlt} from '@fortawesome/free-regular-svg-icons';

export default class MyCanvas extends Component {
  state = {
    canvasWidth : 1000,
    canvasHeight : 1000,
    layers : [
    ],
    layerSelected : -1,
    selectedLayer : undefined,
    selectedLayerList : [],
    selectedChild : undefined,
    selectedChildList : [],
    childEditor : undefined,
    insertedColor : 'red',
    loadingFonts : true,
    loadingImage : false,
  }

  childEditor = {
    Text : TextEditor,
    Image : ImageEditor
  }

  layerCount = 0;
  loadingImage = 0;

  dummyChild = {
    props : {
      x : 0,
      y : 0
    }
  }

  constructor(props) {
    super(props);

    this.changeSize = this.changeSize.bind(this);
    this.downloadImage = this.downloadImage.bind(this);
    this.buttonClick = this.buttonClick.bind(this);

    this.addLayer = this.addLayer.bind(this);
    this.removeLayer = this.removeLayer.bind(this);
    this.selectLayer = this.selectLayer.bind(this);

    this.addChild = this.addChild.bind(this);
    this.removeChild = this.removeChild.bind(this);
    this.selectChild = this.selectChild.bind(this);
    this.editChild = this.editChild.bind(this);
    this.editChildKey = this.editChildKey.bind(this);

    this.selectColor = this.selectColor.bind(this);
  }

  componentDidMount() {
    console.log('Component Mount');

    let FontObserver = [
      'Libre Franklin'
    ]

    function loadFont (fonts, idx) {
      if (idx == fonts.length) {
        console.log('Fonts Loaded');
        this.setState({ loadingFonts : false });
      }
      else {
        let font = new FontFaceObserver(fonts[idx]);

        font.load().then(() => {
          console.log(`Loaded ${fonts[idx]}`);
          loadFont(fonts, idx + 1);
        }).catch((err) => {
          console.log(`Error loading font ${fonts[idx]}`);
          console.log({...err});
        })
      }
    }

    loadFont = loadFont.bind(this);

    loadFont(FontObserver, 0);

  }

  downloadImage (event) {
    const url = this.mycanvas.toDataURL();
    this.downloadLink.href = url;
    this.downloadLink.download = 'file.png';
    this.downloadLink.target = '_blank';
    this.downloadLink.click();
  }

  addLayer (event) {
    this.setState({
      layers : this.state.layers.concat({
        name : `Layer ${this.layerCount}`,
        id : this.layerCount,
        child : [],
        totalChild : 0,
        childCounter : {}
      })
    });

    this.layerCount++;
  }

  removeLayer (event) {

    const { selectedLayer, layers, selectedLayerList } = this.state;
    if (selectedLayer) {

      this.setState({

        layers : layers.filter((item) => { return !selectedLayerList.includes(item.id) }),
        layerSelected : -1,
        selectedLayer : undefined,
        selectedLayerList : []
      });
    }
  }

  addChild(props, renderer) {

    let layer = this.state.selectedLayer;

    const childCounter = layer.childCounter[renderer] || 0;
    layer.child.push({
      props,
      renderer,
      id : layer.totalChild++,
      key : childCounter,
      name : `${renderer} ${childCounter}`
    });
    layer.childCounter[renderer] = childCounter + 1;

    this.setState({
      selectedLayer : layer
    });
  }

  removeChild (event) {

    const { selectedChild, selectedLayer } = this.state;
    if (selectedChild) {

      selectedLayer.child = selectedLayer.child.filter((item) => { return item.name != selectedChild.name });
      this.setState({
        selectedChild : undefined,
        selectedLayer
      });
    }
  }

  editChild (key, value) {
    if (this.state.selectedChild) {
      let selectedChild = this.state.selectedChild;
      selectedChild.props[key] = value;

      this.setState({
        selectedChild
      })
    }
  }

  editChildKey (key) {
    const editChildValue = (value) => {
      this.editChild(key, value);
    }

    return editChildValue;
  }

  selectColor (colors) {
    console.log(colors);
  }

  selectLayer (event) {
    const layerKey = parseInt(event.target.name);
    const { shiftKey, ctrlKey } = event;
    const { selectedItemList : selectedLayerList, selectedItem : selectedLayer } = selectItem({
      itemKey : layerKey,
      itemList : this.state.layers,
      ctrlKey : ctrlKey || false,
      shiftKey : shiftKey || false,
      selectedItem : this.state.selectedLayer,
      selectedItemList : this.state.selectedLayerList
    });

    if (selectedLayerList.length == 1) {

      this.setState({
        layerSelected : layerKey,
      });
    } else {

      this.setState({
        layerSelected : -1,
      });
    }

    this.setState({
      selectedLayerList,
      selectedLayer,
      selectedChild : undefined,
      selectedChildList : [],
      childEditor : undefined
    });
  }

  selectChild (event) {
    let childName = event.target.name;
    const { ctrlKey, shiftKey } = event;
    let selectedChild = this.state.selectedLayer.child.filter((child) => child.name == childName)[0];
    const { selectedItemList : selectedChildList } = selectItem({
      itemKey : selectedChild.id,
      itemList : this.state.selectedLayer.child,
      ctrlKey : ctrlKey || false,
      shiftKey : shiftKey || false,
      selectedItem : this.state.selectedChild,
      selectedItemList : this.state.selectedChildList
    });

    if (!ctrlKey && !shiftKey) {
      this.setState({
        selectedChild,
        childEditor : this.childEditor[selectedChild.renderer]
      });
    }

    this.setState({
      selectedChildList
    });
  }

  buttonClick(event) {
    this.setState({
      'numbers' : this.state.numbers.concat([this.state.numbers.length])
    })
  }

  changeSize(event) {
    this.setState({
      [event.target.name] : event.target.value
    })
  }

  render() {
    const flexChild = {
      display : 'inline-flex',
      alignItems : 'flex-start'
    }

    const layerToolStyle = {
      backgroundColor : 'green',
      border : 'none'
    }

    const { loadingFonts, layerSelected, selectedLayerList, selectedChildList } = this.state;
    const selectedLayer = (layerSelected != -1) ?
      this.state.layers.filter((item) => item.id == layerSelected)[0] : {
      child : []
    };

    const selectedChild = this.state.selectedChild || this.dummyChild;

    return(
      <div ref={this.props.refCall} className='row'>
        <div style={{ display : 'None' }} className='col-12'>
          <a ref={ref => this.downloadLink = ref}/>
          <input ref={ref => this.fileInput = ref} input='file' />
        </div>
        <div className='col-12'>
          <div id='canvas-controller'>
            <label>Width : &nbsp;</label>
            <input type='number' name='canvasWidth' value={this.state.canvasWidth} onChange={this.changeSize}/>
            &nbsp;
            <label>Height : &nbsp;</label>
            <input type='number' name='canvasHeight' value={this.state.canvasHeight} onChange={this.changeSize}/>
            <input style={{ marginLeft: '10px' }} type='submit' name='download' value='Download Image' onClick={this.downloadImage} />
          </div>
        </div>
        <div className='col-12'>
          <div id='child-editor'>
          {
          (this.state.childEditor) ? 
            <this.state.childEditor callback={this.editChildKey} selectedChild={selectedChild} {...selectedChild.props} /> : null
          }
          </div>
          &nbsp;
        </div>
        <div className='col-4'>
          <div className='row'>
            <div className='col-6'>
              <div id='layer-container'>
              {
                this.state.layers.map((item) => 
                  <Button key={item.id} name={item.id} style={{
                    width : '100%',
                    borderColor : 'transparent',
                    background : (selectedLayerList.includes(item.id)) ? 'green' : '#007bff'
                  }}
                  onClick={this.selectLayer}>{item.name}</Button>
                )
              }
              </div><br/>
              <div>
              <ToolButton onClick={this.addLayer} tips='Add layer'>
                <FontAwesomeIcon icon={faPlusSquare} />
              </ToolButton>
              <ToolButton onClick={this.removeLayer} tips='Delete Layer(s)'>
                <FontAwesomeIcon icon={faTrashAlt} />
              </ToolButton>
              </div>
            </div>
            <div id='toolbar-container' className='col-3'>
              <div style={{ display : (layerSelected != -1) ? 'unset' : 'none' }}>
                <PhotoToolbar
                addCircle={this.addCircle}
                addText={this.addText}
                addImage={this.addImage}
                />
              </div>
            </div>
            <div className='col-3' style={{ padding : '0px' }}>
              <div id='child-container'>
              {
                selectedLayer.child.map((child, idx) => 
                <div key={idx}><Button onClick={this.selectChild} name={child.name}
                  style={{
                    width : '100%',
                    borderColor : 'transparent',
                    background : (selectedChildList.includes(child.id)) ? 'green' : '#007bff'
                  }}
                  >{child.name}</Button></div>
              )
              }
              </div><br/>
              <div>
              <ToolButton onClick={this.removeChild} tips='Delete Child(s)'>
                <FontAwesomeIcon icon={faTrashAlt} />
              </ToolButton>
              </div>
            </div>
            <div style={{ top : '10px' }} className='col-12'>
              <UniversalEditor {...selectedChild.props} callback={this.editChildKey} />
            </div>
            <div style={{ top : '20px' }} className='col-12'>
              <ColorPicker getColorCallback={this.selectColor} width='200px' />
            </div>
          </div>
        </div>
        <div className='col-8'>
          <div id='my-canvas'>
          <Stage ref={ref => this.mycanvas = ref} width={this.state.canvasWidth} height={this.state.canvasHeight}>
            {
              this.state.layers.map((item) => 
              <MyLayer child={item.child} key={item.id} />
              )
            }
          </Stage>
          </div>
        </div>
      </div>
    )
  }

  addCircle = (event) => {
    const defaultCircle = {
      fill : 'red',
      width : 50,
      height : 50
    }

    this.addChild(defaultCircle, Circle);
  }

  addText = (event) => {
    const defaultText = {
      fontSize : 16,
      width : 500,
      fontFamily : 'Libre Franklin',
      text : 'Default Text',
      align : 'left'
    }
    
    this.addChild(defaultText, Text);
  }

  addImage = (event) => {
    this.loadImageWaiting();
    let image = new Image();
    image.src = 'https://freesvg.org/img/Placeholder.png';
    let defaultImage = {
      width : 100,
      height : 100
    }
    image.alt = 'placeholder';
    image.onload = () => {
      defaultImage['image'] = image;
      this.loadImageLoaded();
    }

    this.addChild(defaultImage, KonvaImage);
  }

  loadImageWaiting = () => {
    if (this.loadingImage == 0) {
      this.setState({
        loadingImage : true
      });
    }
    this.loadingImage++;
  }

  loadImageLoaded = () => {
    this.loadingImage--;
    if (this.loadingImage == 0) {
      this.setState({
        loadingImage : false
      });
    }
  }
}

class MyLayer extends Component {
  state = {
    child : []
  }

  constructor(props) {
    super(props);

    this.changeEvent = this.changeEvent.bind(this);
    this.addChild = this.addChild.bind(this);
  }

  addChild(props, renderer) {
    console.log('Child added');
    this.setState({
      child : this.state.child.concat([{
        props : props,
        renderer : renderer
      }])
    });
  }

  changeEvent(event) {

  }

  render() {

    return (
      <Layer>
      {
        this.props.child.map((child, idx) => 
          <child.renderer key={idx} {...child.props} />
        )
      }
      </Layer>
    )
  }
}

const LayerButton = (props) => {

  const buttonStyle = (props);

  return (
    <></>
  )
}

